<template>
  <v-card
    height="100%"
  >
    <div
      id="investigacion-dedicacion"
    >
      <v-card-title class="breakword">
        <v-row no-gutters>
          <v-col cols="11">
            Tipo de dedicación.
          </v-col>
          <v-col
            cols="1"
          >
            <v-tooltip
              bottom
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  id="downloadBtn"
                  icon
                  fab
                  small
                  v-bind="attrs"
                  v-on="on"
                  @click="downloadIndicador('investigacion-dedicacion')"
                >
                  <v-icon>{{ mdiDownload }}</v-icon>
                </v-btn>
              </template>
              <span>Descargar indicador.</span>
            </v-tooltip>
          </v-col>
        </v-row>
      </v-card-title>
      <v-card-subtitle>Distribución de los/as investigadores/as.</v-card-subtitle>

      <v-card-text>
        <v-responsive>
          <GChart
            :data="chartData"
            :options="chartOptions"
            type="PieChart"
          />
        </v-responsive>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">
                  Tipo de dedicación
                </th>
                <th class="text-left">
                  Cargos
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in dedicacionTipoData"
                :key="index"
              >
                <td>{{ parseDedicacionTipo(index) }}</td>
                <td>{{ item }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </div>
  </v-card>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import { mdiDownload } from '@mdi/js'
export default {
  data: () => ({
    mdiDownload
  }),
  computed: {
    ...mapState(['dark', 'colors']),
    ...mapGetters(['investigadoresCargos', 'mobileSize']),
    chartData () {
      return [
        ['Tipo de dedicación', 'Docentes'],
        ['Exclusiva', this.dedicacionTipoData.exc],
        ['Semiexclusiva', this.dedicacionTipoData.sem]
      ]
    },
    chartOptions () {
      return {
        chartArea: {
          left: 40,
          right: 40,
          top: 40,
          bottom: 40
        },
        colors: this.colors,
        backgroundColor: { fill: 'transparent' },
        height: 350,
        bar: { groupWidth: '70%' },
        pieHole: '0.2',
        sliceVisibilityThreshold: 0.0001,
        legend: {
          position: 'right',
          alignment: 'center',
          maxLines: 3,
          textStyle: {
            color: this.dark ? '#FFF' : '#000'
          }
        }
      }
    },
    dedicacionTipoData () {
      return this.investigadoresCargos.reduce((acc, cur) => {
        return cur.descripcion_dedicacion === 'Exclusiva'
          ? { ...acc, exc: acc.exc + 1 }
          : cur.descripcion_dedicacion === 'Semi-Exclusiva' || cur.descripcion_dedicacion === 'Semi - Excl Conven'
            ? { ...acc, sem: acc.sem + 1 }
            : acc
      },
      {
        exc: 0,
        sem: 0
      }
      )
    }
  },
  methods: {
    downloadIndicador (indicador) {
      this.$store.dispatch('createPng', indicador)
    },
    parseDedicacionTipo (v) {
      return v === 'exc'
        ? 'Exclusiva'
        : v === 'sem'
          ? 'Semiexclusiva'
          : v === 'total'
            ? 'Total'
            : 'No declara'
    }
  }
}
</script>
